<template>
  <el-cascader
    popper-class="industry-cascade"
    v-model="getValue"
    :placeholder="placeholder"
    :props="industryProps"
    :options="industryList"
    :disabled="disabled"
    filterable
    clearable
    style="width: 100%"
  ></el-cascader>
</template>

<script lang="ts" setup>
import { ref } from "vue";
import { useModel } from "@assets/useHook";
import { industryTreeApi } from "@api/business-management-api";
const props = defineProps({
  modelValue: {
    type: [String, Number],
    required: true,
  },
  emitPath: {
    type: Boolean,
    default: false,
  },
  disabled: {
    type: Boolean,
    default: false,
  },
  placeholder: {
    type: String,
    default: "请选择所属行业",
  },
});
const emits = defineEmits(["update:modelValue"]);
const industryList = ref<any>([]);
const industryProps = {
  value: "id",
  label: "name",
  children: "children",
  checkStrictly: false,
  emitPath: props.emitPath,
};

// const getValue = computed({
//   get: () => props.modelValue,
//   set: (n) => {
//     emits("update:modelValue", n);
//   },
// });
const getValue = useModel(props, "modelValue", emits);
const queryIndustryTree = async () => {
  const { data } = await industryTreeApi.getData();
  industryList.value = industryTreeApi.handleData(data);
};
queryIndustryTree();
</script>

<style lang="scss">
.industry-cascade {
  max-width: 605px;
  overflow: auto;
}
</style>
